/**
 * @explain
 * This storybook decorator is used to provide a component which require a
 * button or control to return focus to.
 */
import { FC } from 'react';

/**
 * Storybook decorator which adds a button
 */

export const FOCUS_SELECTOR = '[name=returnFocus]';

export const FocusReturn = (Story: FC) => {
  return (
    <>
      <button name="returnFocus" type="button">
        Return focus
      </button>
      <Story />
    </>
  );
};
